<template>
  <div class="hello">
    <p>Widget Chatbot Laboratory ðŸ˜Ž</p>
    
    <div class="chat">
      <beautiful-chat
        :participants="participants"
        :titleImageUrl="titleImageUrl"
        :onMessageWasSent="sendMessage"        
        :messageList="messageList"
        :newMessagesCount="newMessagesCount"
        :isOpen="isChatOpen"
        :close="closeChat"
        :open="openChat"
        :showEmoji="true"
        :showFile="false"
        :showTypingIndicator="showTypingIndicator"
        :colors="colors"
        :alwaysScrollToBottom="alwaysScrollToBottom"
        :messageStyling="messageStyling"
        :title="title"
        :placeholder="placeholder"        
        @onType="handleOnType"               
         >
          <slot>
            <p>Blaze Zero</p>
          </slot>
        </beautiful-chat>
    </div>
  </div>
</template>

<script>
const config = require('../config/config')
const converstationStorage = require('../config/conversationStorage')

export default {
  name: 'HelloWorld',
  data() {
    return {
      title: 'Azka Assistant',
      placeholder: 'Escribe tu mensaje...',
      participants: [       
      ], // the list of all the participant of the conversation. `name` is the user name, `id` is used to establish the author of a message, `imageUrl` is supposed to be the user avatar.
      titleImageUrl: 'https://firebasestorage.googleapis.com/v0/b/webpage-9a255.appspot.com/o/images%2Feddy-avatar.jpg?alt=media&token=2d1d25d0-1b9f-4c20-8c87-e322a4a579a8',
      messageList: converstationStorage.init(config.messagesDB),
      newMessagesCount: 0,
      isChatOpen: false, // to determine whether the chat window should be open or closed
      showTypingIndicator: '', // when set to a value matching the participant.id it shows the typing indicator for the specific user
      colors: {
        header: {
          bg: '#4e8cff',
          text: '#ffffff'
        },
        launcher: {
          bg: '#4e8cff'
        },
        messageList: {
          bg: '#ffffff'
        },
        sentMessage: {
          bg: '#4e8cff',
          text: '#ffffff'
        },
        receivedMessage: {
          bg: '#eaeaea',
          text: '#222222'
        },
        userInput: {
          bg: '#f4f7f9',
          text: '#565867'
        }
      }, // specifies the color scheme for the component
      alwaysScrollToBottom: true, // when set to true always scrolls the chat to the bottom when new events are in (new message, user starts typing...)
      messageStyling: true // enables *bold* /emph/ _underline_ and such (more info at github.com/mattezza/msgdown)
    }
  },
  updated(){
    converstationStorage.showDebug(config.messagesDB)
  },
  methods: {
    async sendMessage (message) {
      console.log("Emojis")
      console.log(message)
      const text = (message.data.text) ? message.data.text : message.data.emoji
      

      if (text.length > 0) {
        //Here will be Fetch x)
        
        this.newMessagesCount = this.isChatOpen ? this.newMessagesCount : this.newMessagesCount + 1
        this.onMessageWasSent({ author: 'me', type: 'text', data: { text } })
        await this.sendMessageServer( text)
        //Scroll to Down
      }
    },
    sendMessageServer( text){
      const self = this
      return new Promise((resolve, reject) => {
        fetch(`${config.api}/`, {
          method: 'POST',
          body: JSON.stringify(
            {message: text}
          ),
          headers: {
            'Content-Type': 'application/json; charset=utf-8',                   
          }
        })            
        .then(res => res.json())     
        .then(data => {          
          const responses = data.messages
          for (let response of responses) {

              self.messageList.push( response)
              self.newMessagesCount = self.newMessagesCount + 1
              //Save in Local Storage
              converstationStorage.save(config.messagesDB, self.messageList )            
              
            }           
            resolve(data)                   
          
        })
        .catch(err => reject(err))
      })
    },
    onMessageWasSent (message) {
      // called when the user sends a message
      this.messageList = [ ...this.messageList, message ]
    },
    openChat () {
      // called when the user clicks on the fab button to open the chat
      this.isChatOpen = true
      this.newMessagesCount = 0
    },
    closeChat () {
      // called when the user clicks on the botton to close the chat
      this.isChatOpen = false
    },
    handleScrollToTop () {
      // called when the user scrolls message list to top
      // leverage pagination for loading another page of messages
    },
    handleOnType () {
      //console.log('Emit typing event')
    }
  }
}
</script>

