/**
 * environment variable is designed to point the URL of the Backend
 *  dev : It's normally localhost or ngrok
 *  qa: It's your Development Environment deployed in some place like Heroku, IBM Cloudfoundry, etc.
 *  prod: Production environment!
 */


const environment = 'qa' // This variable can contain dev | qa | prod
const messagesDB = 'messages' //Identifier for messages in LocalStorage

const API_DEV = 'http://localhost:4001/api/chatbot/web/ibm/eddyminator'
const API_QA = 'https://kio-sapsf-2.herokuapp.com/api/chatbot/web/ibm/eddyminator'
const API_PROD = 'http://something/api/v2/web/itam'

const getEnvironment = () => {
  if(environment === 'dev') return API_DEV
  if(environment === 'qa') return API_QA
  if(environment === 'prod') return API_PROD
}

module.exports = {
  api: getEnvironment(),
  messagesDB
}