
const init = ( item ) => {
  try{
    const data = invoke( item )

    if( data){
      return invoke( item)
    }else{      
      const welcomeMessage = [      
        { type: 'text', author: `support`, data: { text: `Bienvenido! Escribe tu mensaje y te ayudarÃ© en lo que pueda` } },      
      ]
      save( item , welcomeMessage)
      return invoke( item) 
    }
  }catch( exception){
    console.error("Exception conversationInitializer")
    console.error(exception)
  }

}

const save = ( item, element ) =>{    
  
  localStorage.setItem( item, JSON.stringify( element))
  
}

const invoke = ( item ) => {
  return JSON.parse( localStorage.getItem( item))
}
const showDebug = (item) =>{
 // console.log("Type")
 // console.log( typeof JSON.parse( localStorage.getItem( item)))

  console.log("Data")
  console.log( JSON.parse( localStorage.getItem( item)))

  return {
    type: typeof JSON.parse( localStorage.getItem( item)),
    data: JSON.parse( localStorage.getItem( item))
  }
}
module.exports = {
  init,
  save,
  showDebug 
}